import React, { useState } from "react"
import { Link } from "gatsby"
import Container from "../layout/container"
import ChevronDown from "../../../svg/chevron-down.svg"
import { CSSTransition } from "react-transition-group"
import "./header.css"

const chapters = [
  { link: "/chapter-1/", name: "Chapter One - The Storm" },
  { link: "/chapter-2/", name: "Chapter One - Out of the Pan" },
]

export default () => {
  const [header, setHeader] = useState({ open: false, openClass: "" })

  function onDropwDown() {
    if (header.open) {
      setHeader({ open: false, openClass: "" })
    } else {
      setHeader({ open: true, openClass: "header--open" })
    }
  }

  return (
    <nav className="header">
      <Container className="header__inner">
        <Link to="/" className="header__link">
          Home
        </Link>
        <button
          className={`header__link header__link--dropdown ${header.openClass}`}
          onClick={onDropwDown}
        >
          Chapters{" "}
          <ChevronDown className={`header__link__svg ${header.openClass}`} />
        </button>
        <a className="header__link">Characters</a>
        <a className="header__link">Powers</a>
        <a className="header__link">Reviews</a>

        <CSSTransition
          in={header.open}
          timeout={250}
          classNames="chapter-list"
          unmountOnExit
          mountOnEnter
        >
          <ol className="chapter-list">
            {chapters.map(chapter => (
              <li key={chapter.link} className="chapter-list__item">
                <Link className="header__link" to={chapter.link}>
                  {chapter.name}
                </Link>
              </li>
            ))}
          </ol>
        </CSSTransition>
      </Container>
    </nav>
  )
}
