import React from "react"
import styled from "styled-components"

export default ({
  colWidth = "calc(50% - 10px)",
  gutter = "10px",
  children,
}) => {
  const Col = styled.div`
    width: calc(${colWidth} - ${gutter} * 2);
    margin-right: ${gutter};
    margin-left: ${gutter};
  `

  return <Col>{children}</Col>
}
